*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

a{
  text-decoration: none;
}

body{
  background: black;
}

body::-webkit-scrollbar { width: 0 !important }

.prim-font{
  font-family: 'Inconsolata', monospace;
}

.prim-color{
  color:rgb(9, 83, 136);
}

.capitalize {
  text-transform: capitalize;
}

.c-header{  
  height: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  width: 100%;
  position: fixed;
  top: 0;
  transition: top 0.3s;
  z-index: 1;
}

.c-menu-links-container{
  margin-right: 50px;
}

.c-spacex-logo{
  height: 90px;
}

.c-menu-links{
  display: flex;
  justify-content: space-between;
  width: 200px;
  list-style-type: none;
  align-items: center;
  font-size: 1.2em;
  font-weight: 700;
  color: rgb(9, 83, 136);
  cursor: pointer;
  margin-right: 100px;
}

.c-menu-links a{
  text-decoration: none;
  color: #185288;
}

.c-menu-links li:hover{
  border-bottom: 3px solid rgb(9, 83, 136);
}



.content-image-overlay{
  background: url('./graphics/mission-image-placeholder.jpg');
  background-size: cover;
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  opacity: 0.25;
  /* transition: linear 1s; */
}

.animate-overlay-fadein{
  animation-name: fadein;
  animation-duration: 1s;
}

.animate-overlay-fadeout{
  animation-name: fadeout;
  animation-duration: 1s;
}

@keyframes fadein{
  0%{opacity: 0;}
}

@keyframes fadeout{
  100%{opacity: 0;}
}

/* for main content */

.c-main-content-container{
  width: 90%;
  color: white;
  padding: 50px;
  max-width: 900px;
  margin: 0 auto;
  margin-top: 100px;
}

.c-main-content-header{
  margin: 20px 0;
  font-size: 3em;
  /* font-size: 700; */
}

.c-mission-name{
  font-size: 3em;
  margin: 20px 0;
}

.c-mission-story{
  font-size: 1.5em;
  line-height: 43px;
}

.c-view-missions-timeline{
  margin: 40px 0;
  padding: 20px 0;
  border: 2px solid #A6242F;
  border-radius: 50px;
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  cursor: pointer;
  text-decoration: none;
  color: white;
  font-size: 1.2em;
}

/* navigation */
.c-navigations{
  display: flex;
  justify-content: flex-start;
  /* color: rgb(75, 75, 75); */
}

.c-navigations section{
  display: flex;
  align-items: center;
  /* font-size: 1.5em; */
  margin-right:20px;
  cursor: pointer;
  height: 50px;
}

.c-navigations section:hover{
  border-bottom: 2px solid white;
}

/* footer */

.c-footer-credit{
  position: fixed;
  bottom: 0;
  left: 0;
  background:#000000;
  width: 100%;
  display: flex;
  justify-content: space-around;
  height: 50px;
  align-items: center;
  color: rgb(65, 65, 65);
  z-index: 1;
}


/* for timeline and extended details */

.c-line{
  width: 2px;
  height: 100vh;
  position: fixed;
  background:#ffffff;
  top: 0;
  margin-left: 6px;
}

.c-circle-point{
  width: 15px;
  height: 15px;
  border-radius: 100%;
  background: #ffffff;
}

.c-circle-point-container{
  height: 100px;
  position: relative;
  left: -203px;
  top: 262px; 
}

.c-main-content-extended{
  position: relative;
  top: 121px;
  left: -14%;
  margin-bottom: 50px
}

.c-extended-details-container{
  width: 90%;
  color: white;
  padding: 50px;
  max-width: 900px;
  margin: 0 auto;
  position: relative;
  left: -55px;
}

