/* New Start Content */

.c-start-content-container{
position: absolute;
top: 25%;
left: 15%;
color: white;
display: flex;
justify-content: space-between;
flex-direction: column;
margin-bottom: 90px;
}

.c-start-project-motivations, .c-start-content-header{
  margin: 40px 0;
  width: 50%;
}

.c-start-content-header{
  font-size: 2em;
}

.c-start-project-motivations{
  font-size: 1.5em;
}

.c-start-motivation{
  opacity: 0.3;
  background-size: cover;
}

.c-icon-rocket-up{
  background: url(./graphics/rocket-3-A.png);
  background-size: cover;
  width: 80px;
  height: 80px;
  position: fixed;
  bottom: 80px;
  cursor: pointer;
  left: 5%;
  display: none;
}

.rocket-move{
  animation-name: rocketmove;
  animation-duration: 3s;
}

.c-leadCaption-expanded{
  font-size: 3em;
  position: relative;
  left: -14%;
  /* border-bottom: rgb(0, 25, 107) solid 3px; */
  /* width: 50px; */
  top: 95px;
}

.c-goback-catalog{
  color: #414141;
  cursor: pointer;
}

.c-goback-catalog:hover{
  color: #ffffff;
  border-bottom: white solid 3px;
}

@keyframes rocketmove{
  0%{bottom: 80px;}
  100%{bottom:120%}
}

/* outer links */

.c-links-out a img{
  width: 50px;
  height: 50px;
  margin: 10px;
  cursor: pointer;
  background-size: cover;
}

.c-links-out{
  margin-top: 40px;
}

@media screen and ( max-width: 1300px) {

  .c-leadCaption-expanded {
    margin-left: 0;
    padding-right: none;
    left: 14px;
  }
  
  .c-extended-details-container{
      margin-left: 0;
      padding-right: none;
      left: 0;
 
  }
  
  .c-main-content-extended{
      left: 14%;
  }

  .c-circle-point-container{
      left: 0;
  }

  /* .c-header{
      flex-direction: column;
  } */

  .c-menu-links{
      width: 100%;
      padding: 10px;
  }
}

@media screen and ( max-width: 1660px) {
  .c-icon-rocket-up{
      display: none!important;
  }
}